import { Routes, Route, BrowserRouter } from 'react-router-dom';
import PrivateRoute from './routerPrivate';
import PublicRoute from './routerPublic';
//import NotFount from '../views/notFound';


const AppRouter = () => {
    return (
        <BrowserRouter>

            <Routes>

                <Route
                    path='*'
                    element={<PublicRoute />}
                />

                <Route
                    path='privada/*'
                    element={<PrivateRoute />}
                />

               
            </Routes>

        </BrowserRouter>
    )
}

export default AppRouter
