import React, { useState } from 'react';
import 'antd/dist/antd.min.css'
import App from '../../components/molecula/Tablaa/index.js'
import AppLogin from '../../components/molecula/Search/index.js';
import { imagenL } from '../../constante/imagen.js';

//"../../components/molecula/Tablaa";
const functionOriginData = () => {
    let originData = [];
    for (let i = 0; i < 100; i++) {
      originData.push({
        key: i.toString(),
        name: `All Tow Services ${i}`,
        age: 32,
        address: `London Park no. ${i}`,
      });
    }
    return originData;
  }

const Index = () => {
    const [originData, setOriginData] = useState(functionOriginData());
    const [searchData, setSearchData] = useState("")
    
  
    return (
    
    <div style={{background:"#161616"}}>
      
     <div style={{
         marginLeft:"16%",
         color:"white",
         fontWeight:"700",
         fontSize:"20px",
         fontFamily:"arial"}} >
        <img style={{
         width:"20%",
         verticalAlign: "inherit", 
         marginLeft: "auto",
         display: "block", 
         marginRight:"10%",
        marginBottom:"-5rem",}} 
         src={imagenL.IMAGENICON}></img>

        <div style={{ display:"flex", justifyContent:"center", marginLeft:"-20%"}} >
            <AppLogin
              searchData = { searchData }
              setSearchData = {setSearchData}
            />


        </div>

          <div style={{fontFamily:"arial"}}> 
        UberTow Registred Companies

        </div>

        </div>
 
    <div style={{
        display:"flex",
        justifyContent:"center",
        alignItems:"center",}}>
    
    <div style={{
        width:"70%",
        borderRadius:"20px",
        }}>

             
       <App
            originData={originData}
            setOriginData={setOriginData}
            searchData={searchData}
       />
        
         </div>

      </div>
      </div>

 
    );
};

export default Index;



      